.div {
  width: 200px;
  height: 200px;
  background-image: url(/static/media/me.64446d8d.png);
  background-size: cover;
  cursor: -webkit-grab;

}

.div:active {
  cursor: -webkit-grabbing;
}

.bm-item {
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: 2px solid #66FCF1;
  border-radius: 0.6em;
  color: #66FCF1;
  cursor: pointer;
  display: flex;
  align-self: center;
  font-size: 1rem;
  margin-bottom: 10px;
  padding: 1.2em 2.8em;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  }



/* Change color on hover */
.bm-item:hover, .bm-item:focus {
  color: #fff;
  outline: 0;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 20px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #66FCF1;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bbecea;
}

/* General sidebar styles */
.bm-menu {

  background: #060f0f;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #060f0f;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


@media (orientation: portrait) {
  
