       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  
  }
  
  .Contact h1 {
    margin-top: 40%;
    color: #66FCF1;
    font-size: 3vmax;
    font-family: 'Montserrat', sans-serif;
    max-width: 100%;
    max-height: 100%;
    margin-left: 10%;
    margin-right: 10%;
    cursor: default;
  }
  
  .Contact p {
    text-align: center;
    max-width: 100%;
    font-size: 15px;
    color: #C5C6C7;
    font-family: 'Montserrat', sans-serif;
    cursor: default;
  }
  
  .Contact a {
    font-weight: bold;
    font-size: 20px;
    color: #66FCF1;
    font-family: 'Montserrat', sans-serif;
    max-width: 100%;
  }
  
  .Contact img {
    margin-top: 1%;
    width: 10%;
  }

}

